<template>
<div class="">
    <div v-if="!source" >
        <!-- <h1>Hello World</h1> -->
        <img style="" src="../assets/arrow.svg" style="width: 160px;">
    </div>
    <div v-if="showSpinner == true">
        <div class="spinner">
            <div class="bounce1"></div>
            <div class="bounce2"></div>
            <div class="bounce3"></div>
        </div>
    </div>
    <div class="card mb-3" v-for="article in articles">
        <a v-bind:href="article.url" target="_blank">
            <img style="width: 100%; height: 200px;" v-bind:src="article.urlToImage">
            <div class="card-body">
                <h5 class="card-title">{{article.title}}</h5>
                <p class="card-text">{{article.description}}</p>
                <p><em>{{article.author}}</em></p>
            </div>
            <div class="card-footer text-muted">
                {{article.publishedAt}}
            </div>
        </a>
    </div>
</div>
</template>
<script>
import SourceSelection from './SourceSelection';
export default {
	name: 'newslist',
	props: ['source'],
	data() {
		return {
			articles: [],
			showSpinner: false,
		};
	},
	methods: {
		updateSource: function(source) {
			this.$http
				.get(
					'https://newsapi.org/v2/top-headlines?sources=' +
						source +
						'&apiKey=028f528a2dd04b0bb3192c9b9a768e28'
				)
				.then(response => {
					this.articles = response.data.articles;
				});
		},
	},
	created: function() {
		this.updateSource(this.source);
	},
	watch: {
		source: function(val) {
			this.updateSource(val);
		},
	},
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.media-object {
	width: 128px;
	padding: 10px;
}
.media {
	border-top: 1px solid lightgray;
	padding-top: 20px;
}

/* Loader CSS */

.spinner {
	/* margin: 100px auto 0; */
	width: 100%;
	text-align: center;
}

.spinner > div {
	margin-top: 40px;
	width: 18px;
	height: 18px;
	background-color: white;
	border-radius: 100%;
	display: inline-block;
	-webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
	animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
	-webkit-animation-delay: -0.32s;
	animation-delay: -0.32s;
}

.spinner .bounce2 {
	-webkit-animation-delay: -0.16s;
	animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
	0%,
	80%,
	100% {
		-webkit-transform: scale(0);
	}
	40% {
		-webkit-transform: scale(1);
	}
}

@keyframes sk-bouncedelay {
	0%,
	80%,
	100% {
		-webkit-transform: scale(0);
		transform: scale(0);
	}
	40% {
		-webkit-transform: scale(1);
		transform: scale(1);
	}
}
</style>


