<template>
  <div class="">
    <h3 class="headi">Tha_News</h3>
    <!-- <h3 class="d-inline" style="float: right; padding: 28px 0px 14px 0px;"><span class="fa fa-search"></span></h3> -->
    <div class="form-group">
        <select class="form-control" v-on:change="sourceChanged">
          <option value="">Please select news source ...</option>
          <option v-for="source in sources" v-bind:value="source.id">{{source.name}}</option>
        </select>
    </div>
    <!-- <div class="news-site mb-20">
        <span class="badge badge-secondary tags-style">Al-jazeera</span>
    </div> -->
  </div>
</template>
<script>
export default {
	name: 'sourceselection',
	data() {
		return {
			sources: [],
			source: '',
		};
	},
	methods: {
		sourceChanged: function(e) {
			for (var i = 0; i < this.sources.length; i++) {
				if (this.sources[i].id == e.target.value) {
					this.source = this.sources[i];
				}
			}
			this.$emit('sourceChanged', e.target.value);
		},
	},
	created: function() {
		this.$http.get('https://newsapi.org/v1/sources?language=en').then(response => {
			this.sources = response.data.sources;
		});
	},
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.headi {
	border-bottom: 2px solid #5a5959;
	display: inline-block;
	padding: 28px 0px 14px 0px;
	margin-bottom: 10px;
}
.tags-style {
	margin-bottom: 13px;
}
</style>


